import React from "react";

function Skills({ skills }) {
  return skills && skills.length ? (
    skills.map(item => (
      <article className="pd-16y pd-8x" key={skills.title}>
        <h3 className="fs-18 mr-8b">{item.title}</h3>
        <p className="fs-16 tx-hier-3 mr-8b">{item.skills}</p>
      </article>
    ))
  ) : (
    <p className="fs-16 tx-hier-3 mr-8b">No skills added</p>
  );
}

export default Skills;
