import React from "react";
import { Link } from "react-router-dom";
import { useStoreState } from "easy-peasy";

function MainNav() {
  let authType = useStoreState(state => state.profile.type);
  let { type: isAuthAlter } = useStoreState(state => state.auth.profile);
  return (
    <nav className="sidenav bg-light">
      <div className="sidenav-content">
        <header className="disp-flex fl-dir-col">
          <img
            className="disp-ib mr-16 al-self-c"
            src="./atarvEd.png"
            alt="AthartV-Ed Logo"
            srcset=""
            width="100px"
          />
          <p className="al-self-c fs-14 txt-align-center lh-16">
            {" "}
            Learning Management Module
          </p>
        </header>
        <ul className="nav-list mr-32t mr-32b">
          <li>
            <Link
              to="/activities"
              className={
                window.location.pathname === "/activities"
                  ? "mr-24l pd-16 current-nav"
                  : "mr-24l pd-16"
              }
            >
              <p className="mr-12r disp-ib">Activities</p>
            </Link>
          </li>
          {authType !== "A" && isAuthAlter !== "A" ? (
            <>
              <li>
                <Link
                  to="/myinfo"
                  className={
                    window.location.pathname === "/myinfo"
                      ? "mr-24l pd-16 current-nav"
                      : "mr-24l pd-16"
                  }
                >
                  <p className="mr-12r disp-ib">My info</p>
                </Link>
              </li>
              <li>
                <Link
                  to="/settings"
                  className={
                    window.location.pathname === "/settings"
                      ? "mr-24l pd-16 current-nav"
                      : "mr-24l pd-16"
                  }
                >
                  <p className="mr-12r disp-ib">Settings</p>
                </Link>
              </li>
            </>
          ) : null}
          <li>
            <Link to="/logout" className="mr-24l pd-16">
              <p className="mr-12r disp-ib">Logout</p>
            </Link>
          </li>
        </ul>
      </div>
    </nav>
  );
}

export default MainNav;
