  isTasksLoading: true,
  setTasksLoading: action((state, payload) => {
    state["isTasksLoading"] = payload;
  }),
  tasks: [],
  setTasks: action((state, payload) => {
    state["tasks"] = payload;
  }),
  fetchTasks: thunk(async (action, payload) => {
    try {
      let tasks = await axios.get(`/tasks/${payload}`);

      action.setTasks(tasks.data);
      action.isTasksLoading(false);
    } catch (err) {
      console.log(err);
      action.isTasksLoading(false);
    }
  })
