import React, { useState } from "react";
import { useStoreActions } from "easy-peasy";

function UpdateSkills() {
  let [isVisible, setVisible] = useState(false);
  let [skills, setSkills] = useState("");
  let [title, setTitle] = useState("");
  let updateSkills = useStoreActions(action => action.profile.updateSkills);
  return (
    <>
      <button
        className="blk btn fs-16 mr-16b"
        onClick={e => {
          setVisible(!isVisible);
        }}
      >
        Add a newly aquired skill
      </button>
      {isVisible ? (
        <form
          action=""
          className="bg-lgray card pd-16 mr-32b"
          onSubmit={e => {
            e.preventDefault();
            updateSkills({ skills, title });
          }}
        >
          <h3 className="fs-18 mr-16b">Submit a new skill</h3>
          <label for="skill" className="form-labl">
            Skill type
          </label>
          <input
            type="text"
            className="form-inp"
            name="skill"
            id="skill"
            placeholder="Enter the skill type"
            value={title}
            onChange={e => setTitle(e.target.value)}
          />
          <label for="skill-description" className="form-labl mr-16t">
            Skill description
          </label>
          <input
            type="text"
            className="form-inp"
            name="skill-description"
            id="skill-description"
            placeholder="Describe the skils under this type"
            value={skills}
            onChange={e => setSkills(e.target.value)}
          />
          <section className="disp-flex fl-jc-fe">
            <button
              className="btn disp-ib fs-16 mr-8t mr-12r"
              type="submit"
              onClick={e => {
                setVisible(!isVisible);
              }}
            >
              Cancel
            </button>
            <button className="blk disp-ib btn fs-16 mr-8t">
              Submit skill
            </button>
          </section>
        </form>
      ) : null}
    </>
  );
}

export default UpdateSkills;
