import React, { useState, useEffect } from "react";
import ReportUpdateTask from "./ReportTableRow/ReportUpdateTask/ReportUpdateTask";
import ReportTableRow from "./ReportTableRow/ReportTableRow";
import { useStoreState, useStoreActions } from "easy-peasy";
import ReportTableHeader from "./ReportTableRow/ReportTableHeader/ReportTableHeader";
import LoadingScreenSmall from "../../../../UI/LoadingScreenSmall";
import ReportTableAllTasks from "./ReportTableAllTasks/ReportTableAllTasks";

function ReportTable({
  tasks,
  suid,
  activityid,
  setTasksVisible,
  isTaskLoading
}) {
  let { user_id } = useStoreState(state => state.profile);
  let { fname, lname } = useStoreState(state => state.profile);
  let setActivityTasks = useStoreActions(
    action => action.admin.fetchActivityTask
  );
  let setTasks = useStoreActions(action => action.admin.setTasks);
  let [currentUserId, setCurrentUserId] = useState(user_id);

  let [isUserTasksVisible, setUserTasksVisible] = useState(false);
  let [newTasks, setNTask] = useState([]);
  let [dependency, setDependency] = useState("");
  let [category, setCategory] = useState("");
  let [tdesc, setTDesc] = useState("");

  useEffect(() => {
    if (isTaskLoading) setActivityTasks(activityid);
    setIsTasksAssignable(evaluateTaskAssignable());
    console.log(evaluateTaskAssignable(), isTaskLoading);
  }, [isTaskLoading, tasks, newTasks]);

  function evaluateTaskAssignable() {
    console.log(
      newTasks,
      newTasks.every(task => task.user_id !== user_id)
    );
    return (
      (tasks && tasks.every(task => task.user_id !== user_id)) ||
      (newTasks &&
        newTasks.length &&
        newTasks.every(task => task.user_id !== user_id))
    );
  }

  let [isTaskAssignable, setIsTasksAssignable] = useState(
    evaluateTaskAssignable()
  );

  function setCorrectTask() {
    setNTask([
      {
        user_id: user_id,
        name: fname + " " + lname,
        task_description: tdesc,
        dependency,
        category
      }
    ]);
  }

  function isAnyNewTasks() {
    return newTasks && newTasks.length;
  }

  function isAnyTasks() {
    return (tasks && tasks.length) || (newTasks && newTasks.length);
  }

  return (
    <>
      <table id="task-table" className="br-collapse anim-down">
        <thead>
          {isAnyTasks() || !isAnyNewTasks() ? <ReportTableHeader /> : null}
        </thead>
        <tbody className="card">
          {isAnyTasks() && !isTaskLoading ? (
            [...tasks, ...newTasks].map(task => (
              <ReportTableRow
                setUserTasksVisible={setUserTasksVisible}
                setCurrentUserId={setCurrentUserId}
                setTasksVisible={setTasksVisible}
                task={task}
              />
            ))
          ) : (
            <tr className="br-collapse">
              <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16" colspan={12}>
                {isTaskLoading ? (
                  <>
                    <LoadingScreenSmall />
                  </>
                ) : (
                  <p>Tasks are not yet assigned</p>
                )}
              </td>
            </tr>
          )}
          {!isAnyTasks() ? (
            <ReportUpdateTask
              tdesc={tdesc}
              setTDesc={setTDesc}
              name={fname + " " + lname}
              dependency={dependency}
              setDependency={setDependency}
              category={category}
              setCategory={setCategory}
            />
          ) : null}
        </tbody>
      </table>
      {isAnyNewTasks() ? (
        <button
          onClick={e => {
            setNTask([]);
          }}
          className="btn mr-16"
        >
          Cancel
        </button>
      ) : null}
      {isAnyNewTasks() ? (
        <button
          className="blk btn  mr-16t mr-12r"
          onClick={e => {
            setTasksVisible(false);
            setIsTasksAssignable(evaluateTaskAssignable());
            setTasks({ user_id: newTasks, milestone: "", activityid, suid });
          }}
        >
          Submit report
        </button>
      ) : null}

      {isTaskAssignable && !isAnyNewTasks() ? (
        <button
          className="blk btn  mr-16t mr-12r"
          onClick={e => {
            setCorrectTask();
          }}
        >
          Submit Changes
        </button>
      ) : null}
      {isUserTasksVisible ? (
        <ReportTableAllTasks
          setUserTasksVisible={setUserTasksVisible}
          activityId={activityid}
          userId={currentUserId}
        />
      ) : null}
    </>
  );
}

export default ReportTable;
