import React, { useEffect } from "react";
import { useStoreActions, useStoreState } from "easy-peasy";
import ReportTableRow from "../ReportTableRow/ReportTableRow";
import LoadingScreenSmall from "../../../../../UI/LoadingScreenSmall";

function ReportTableAllTasks({
  userId,
  activityId,
  isUserTasksVisible,
  setUserTasksVisible
}) {
  let fetchAllUserTasks = useStoreActions(
    action => action.admin.fetchAllUserTasks
  );
  let userTasks = useStoreState(state => state.admin.userTasks);
  let isUserTasksLoading = useStoreState(
    state => state.admin.isUserTasksLoading
  );
  let setUserTasksLoading = useStoreActions(
    action => action.admin.setUserTasksLoading
  );
  useEffect(() => {
    if (isUserTasksLoading) fetchAllUserTasks({ userId, activityId });
  }, [isUserTasksLoading]);
  return (
    <>
      <section class="user-modal pd-32">
        <div class="pd-32 test-container bg-light">
          <h3 className="mr-8b fs-14">All the reports of this activity</h3>
          <i
            class="fas fa-times-circle close-btn cu-po"
            onClick={e => {
              setUserTasksVisible(false);
            }}
          ></i>
          <table id="task-table" className="br-collapse anim-down bg-light">
            <tbody className="card">
              {userTasks && userTasks.length && !isUserTasksLoading ? (
                userTasks.map(task => <ReportTableRow task={task} />)
              ) : isUserTasksLoading ? (
                <>
                  <td
                    className="br-sm-blue fs-14 pd-8x pd-4y lh-16"
                    colspan={12}
                  >
                    <LoadingScreenSmall />
                  </td>
                </>
              ) : (
                <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16" colspan={12}>
                  <p>Tasks are not yet assigned</p>
                </td>
              )}
            </tbody>
          </table>
        </div>
      </section>
    </>
  );
}

export default ReportTableAllTasks;
