import React from "react";

function ReportTableHeader() {
  return (
    <tr className="br-collapse">
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Name</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Task Description</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Starting time</th>
      <th className="br-sm-blue-dark fs-14 pd-16x ">Starting status</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Ending time</th>
      <th className="br-sm-blue-dark fs-14 pd-16x ">Ending status</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Dependancy</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">
        Task progress/Completion
      </th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Category</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">New skills gained</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Knowledge aquired</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Update change</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Delete task</th>
      <th className="br-sm-blue-dark fs-14 pd-16x pd-4y">Comment</th>
    </tr>
  );
}

export default ReportTableHeader;
