import React, { useState } from "react";
import { useStoreActions } from "easy-peasy";

function UpdateQualification() {
  let [isVisible, setVisible] = useState(false);
  let [qualification, setQualification] = useState("");
  let [college, setCollege] = useState("");
  let [board, setBoard] = useState("");
  let [grade, setGrade] = useState("");

  let updateQualification = useStoreActions(
    action => action.profile.updateQualification
  );
  return (
    <>
      <button
        className="blk btn fs-16 mr-16b"
        onClick={e => {
          setVisible(!isVisible);
        }}
      >
        Add a educational qualification
      </button>
      {isVisible ? (
        <form
          action=""
          className="bg-lgray card pd-16 mr-32b"
          onSubmit={e => {
            e.preventDefault();
            updateQualification({ qualification, college, grade, board });
          }}
        >
          <h3 className="fs-18 mr-16b">Educational qualification</h3>
          <label for="qualification" className="form-labl">
            Qualification type
          </label>
          <input
            type="text"
            className="form-inp"
            name="qualification"
            id="qualification"
            placeholder="Enter the qualification type"
            value={qualification}
            onChange={e => setQualification(e.target.value)}
          />
          <label for="college" className="form-labl mr-16t">
            College
          </label>
          <input
            type="text"
            className="form-inp"
            name="college"
            id="college"
            placeholder="Enter the college name"
            value={college}
            onChange={e => setCollege(e.target.value)}
          />
          <label for="board" className="form-labl mr-16t">
            Board
          </label>
          <input
            type="text"
            className="form-inp"
            name="board"
            id="board"
            placeholder="Enter your board of education"
            value={board}
            onChange={e => setBoard(e.target.value)}
          />

          <label for="grade" className="form-labl mr-16t">
            Grade
          </label>
          <input
            type="number"
            max={100}
            min={0}
            className="form-inp"
            name="grade"
            id="grade"
            placeholder="Enter grade"
            value={grade}
            onChange={e => setGrade(e.target.value)}
          />
          <section className="disp-flex fl-jc-fe">
            <button
              className="btn disp-ib fs-16 mr-8t mr-12r"
              type="submit"
              onClick={e => {
                setVisible(!isVisible);
              }}
            >
              Cancel
            </button>
            <button className="blk disp-ib btn fs-16 mr-8t">Submit</button>
          </section>
        </form>
      ) : null}
    </>
  );
}

export default UpdateQualification;
