import React, { useState } from "react";
import CustomSelect from "../../../../../UI/CustomSelect";
import { useStoreState, useStoreActions } from "easy-peasy";

function ReportTableRow({
  task,
  setTasksVisible,
  setCurrentUserId,
  setUserTasksVisible
}) {
  let [stime, setSTime] = useState("06:00");
  let [etime, setETime] = useState("06:00");
  let [etask, setETask] = useState("");
  let [stask, setSTask] = useState("");
  let [progress, setProgress] = useState("");
  let [newskill, setNewskill] = useState("");
  let [kgain, setKgain] = useState("");
  let [comment, setComment] = useState("");
  let { user_id } = useStoreState(state => state.profile);
  let updateTask = useStoreActions(action => action.admin.updateTask);
  let setUserTasksLoading = useStoreActions(
    action => action.admin.setUserTasksLoading
  );
  let deleteTask = useStoreActions(action => action.admin.deleteTask);
  let updateComment = useStoreActions(action => action.admin.updateComment);

  function isAuthUser() {
    return user_id === task.user_id;
  }

  return (
    <tr className="br-collapse">
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        <a
          href=""
          onClick={e => {
            e.preventDefault();
            setCurrentUserId(user_id);
            setUserTasksVisible(true);
            setUserTasksLoading(true);
          }}
        >
          {task.name ? task.name : task.fname + " " + task.lname}
        </a>
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {task.task_description}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {isAuthUser() && !task.stime ? (
          <input
            className="wi-100p bg-gray-trans br-none pd-4y pd-8x disp-ib"
            type="time"
            placeholder="hh:mm"
            name="start-time"
            value={stime}
            onChange={e => setSTime(e.target.value)}
          />
        ) : (
          <p>{task.stime || "Not available"}</p>
        )}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {isAuthUser() && !task.status_sod ? (
          <CustomSelect
            options={["Completed", "Started", "Ongoing"]}
            isStatic={true}
            placeholder={"Status"}
            value={stask}
            selectValue={setSTask}
          />
        ) : (
          <p>{task.status_sod || "Not available"}</p>
        )}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {isAuthUser() && !task.etime ? (
          <input
            className="wi-100p bg-gray-trans br-none pd-4y pd-8x disp-ib"
            type="time"
            name="end-time"
            value={etime}
            onChange={e => setETime(e.target.value)}
          />
        ) : (
          <p>{task.etime || "Not available"}</p>
        )}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {isAuthUser() && !task.status_eod ? (
          <CustomSelect
            options={["Completed", "Started", "Ongoing"]}
            isStatic={true}
            placeholder={"Status"}
            value={etask}
            selectValue={setETask}
          />
        ) : (
          <p>{task.status_eod || "Not available"}</p>
        )}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {task.dependency || "Not available"}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {user_id === task.user_id && !task.progress ? (
          <input
            className="wi-100p bg-gray-trans br-none pd-4y pd-8x disp-ib"
            type="number"
            placeholder="xx%"
            name="start-time"
            min={0}
            max={100}
            value={progress}
            onChange={e => setProgress(e.target.value)}
          />
        ) : (
          <p>{task.progress || "Not available"}</p>
        )}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {task.category}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {isAuthUser() && !task.new_skills_acquired ? (
          <textarea
            className="bg-gray-trans br-none pd-4y pd-8x disp-ib"
            name="knowledge-gained"
            value={newskill}
            onChange={e => {
              setNewskill(e.target.value);
            }}
            id=""
            placeholder="Enter the new skills aquired"
          ></textarea>
        ) : (
          <p>{task.new_skills_acquired || "Not available"}</p>
        )}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {isAuthUser() && !task.knowledge_gained ? (
          <textarea
            value={kgain}
            onChange={e => {
              setKgain(e.target.value);
            }}
            className="bg-gray-trans br-none pd-4y pd-8x disp-ib"
            name="knowledge-gainer"
            id=""
            placeholder="Enter the knowledge gained"
          ></textarea>
        ) : (
          <p>{task.knowledge_gained || "Not available"}</p>
        )}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {isAuthUser() ? (
          <button
            className="btn"
            onClick={e => {
              if (isAuthUser()) {
                updateTask({
                  reportId: task.reportid,
                  userId: task.user_id,
                  startStatus: stask || task.status_sod,
                  startTime: stime || task.stime,
                  endStatus: etask || task.status_eod,
                  endTime: etime || task.etime,
                  progress: progress || task.progress,
                  knowledegeGained: kgain || task.knowledge_gained,
                  newSkills: newskill || task.new_skills_acquired
                });
                setTasksVisible();
              }
            }}
          >
            Submit
          </button>
        ) : (
          <p>Not allowed</p>
        )}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {isAuthUser() && setTasksVisible ? (
          <button
            className="btn"
            onClick={e => {
              if (isAuthUser()) {
                deleteTask({
                  reportid: task.reportid
                });
                setTasksVisible();
              }
            }}
          >
            Submit
          </button>
        ) : (
          <p>Not allowed</p>
        )}
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16 txt-align-center">
        {task.assignedby == user_id && !task.comment && setTasksVisible ? (
          <>
            <textarea
              value={comment}
              onChange={e => {
                setComment(e.target.value);
              }}
              className="bg-gray-trans br-none pd-4y pd-8x disp-ib mr-8b"
              name="comment"
              id=""
              placeholder="Enter the knowledge gained"
            ></textarea>
            <button
              className="btn"
              onClick={e => {
                setTasksVisible(false);
                console.log(task);
                updateComment({
                  reportid: task.reportid,
                  comment,
                  user_id: task.user_id
                });
              }}
            >
              Submit comment
            </button>
          </>
        ) : (
          <p>{task.comment || "No comment added yet"}</p>
        )}
      </td>
    </tr>
  );
}

export default ReportTableRow;
