import { action, thunk } from "easy-peasy";
import axios from "./axios.proxy";
export default {
  isAuth: false,
  setType: action((state, payload) => {
    state["type"] = payload;
  }),
  setAuth: action((state, payload) => {
    state["isAuth"] = payload;
  }),
  isSuper: false,
  setSuper: action((state, payload) => {
    state["isSuper"] = payload;
  }),
  setUsers: action((state, payload) => {
    state["users"] = payload;
  }),
  profile: {},
  setProfile: action((state, payload) => {
    state["profile"] = payload;
  }),
  errors: [],
  setErrors: action((state, payload) => {
    state["errors"] = payload;
  }),
  loginSubmit: thunk(async (action, payload) => {
    try {
      let profile = await axios.post("/login", payload);
      action.setProfile(profile.data);
      action.setAuth(true);
      localStorage.setItem("token", profile.data.token);
    } catch (err) {
      console.log(err.response.data);
      action.setErrors(err.response.data);
    }
  }),
  registerSubmit: thunk(async (action, payload) => {
    try {
      let profile = await axios.post("/signup", payload);
      action.setProfile(profile.data);
      action.setAuth(true);
      localStorage.setItem("token", profile.data.token);
    } catch (err) {
      console.log(err.response.data);
      action.setErrors(err.response.data);
    }
  }),
  getAllUsers: thunk(async (action, payload) => {
    axios
      .get("/admin/all", {
        headers: {
          "x-auth-token": localStorage.getItem("token")
        }
      })
      .then(users => {
        action.setUsers(users.data.allUsers);
        delete users.data.allUsers;
        action.setProfile({
          type: "A"
        });
        action.setAuth(true);
      })
      .catch(err => {
        if (err.response.data.type === "email" && typeof payload === "function")
          payload(action.setAuth);
        else {
          action.setType("U");
          localStorage.removeItem("token");
        }
      });
  })
};
