import React from "react";

function WelcomeSection() {
  return (
    <article className="pd-64x pd-64t flex-basis-50-l">
      <h2 className="mr-32b">Welcome</h2>
      <p className="lh-16">
        Please login or register to access the AthrV-Ed portal facility which
        provides tools to track your personal and domain's progress.
      </p>
      <div className="side-note mr-32t lh-16">
        <p>
          Please note - You must be already a core member of AthrV-Ed to be able
          to register
        </p>
      </div>
    </article>
  );
}

export default WelcomeSection;
