import React, { useState, useEffect } from "react";
import { useStoreState, useStoreActions } from "easy-peasy";
import CustomSelect from "../../UI/CustomSelect";
import MainNav from "../MainNav/MainNav";

function Settings() {
  let prevEmail = useStoreState(state => state.profile.email);
  let prevFname = useStoreState(state => state.profile.fname);
  let prevLname = useStoreState(state => state.profile.lname);
  let prevSex = useStoreState(state => state.profile.sex);
  let prevSem = useStoreState(state => state.profile.sem);
  let prevDob = useStoreState(state => state.profile.dob);
  let prevPhone = useStoreState(state => state.profile.phone);
  let prevAddress1 = useStoreState(state => state.profile.address_line1);
  let prevAddress2 = useStoreState(state => state.profile.address_line2);
  let prevCity = useStoreState(state => state.profile.city);
  let prevDist = useStoreState(state => state.profile.dist);
  let prevState = useStoreState(state => state.profile.state);
  let prevPin = useStoreState(state => state.profile.pin);
  let prevaboutme = useStoreState(state => state.profile.aboutme);
  let prevbranch = useStoreState(state => state.profile.branch);
  let isDone = useStoreState(state => state.profile.isDone);
  let setDone = useStoreActions(action => action.profile.setDone);

  let updateProfile = useStoreActions(action => action.profile.updateProfile);

  let [sex, setSex] = useState(prevSex || "Male");
  let [sem, setSem] = useState("" + prevSem || "First1");
  let [branch, setbranch] = useState(prevbranch || "Computer Science");
  let [fname, setFname] = useState(prevFname || "");
  let [lname, setLname] = useState(prevLname || "");
  let [dob, setDob] = useState(new Date(prevDob).toLocaleDateString() || "");
  let [email, setEmail] = useState(prevEmail || "");
  let [phone, setPhone] = useState(prevPhone || "");
  let [address_line1, setaddress_line1] = useState(prevAddress1 || "");
  let [address_line2, setaddress_line2] = useState(prevAddress2 || "");
  let [city, setCity] = useState(prevCity || "");
  let [dist, setDist] = useState(prevDist || "");
  let [state, setSt] = useState(prevState || "");
  let [pin, setPin] = useState(prevPin || "");
  let [aboutme, setaboutme] = useState(prevaboutme || "");

  useEffect(() => {
    setDone(false);
  }, []);

  return (
    <>
      <MainNav />
      <section id="settings" className="flex-basis-70-l bg-lights pd-32">
        <h2 className="mr-8b fs-24">Edit your basic info</h2>
        <form
          id="profile-edit"
          action=""
          className="disp-flex fl-jc-fs"
          onSubmit={async e => {
            e.preventDefault();
            try {
              updateProfile({
                sex,
                branch,
                fname,
                lname,
                sem: sem.match(/\d+/)[0],
                dob,
                email,
                phone,
                address_line1,
                address_line2,
                city,
                dist,
                state,
                pin,
                aboutme
              });
            } catch (err) {
              console.log(err);
            }
          }}
        >
          <section className="mr-12r flex-basis-20-l">
            <label className="mr-16t form-labl" htmlFor="fname">
              First name
            </label>
            <input
              className="mr-8b form-inp"
              type="text"
              name="fname"
              id="fname"
              placeholder="Enter your first name"
              value={fname}
              onChange={e => setFname(e.target.value)}
            />
          </section>
          <section className="flex-basis-20-l mr-12r ">
            <label className="mr-16t form-labl" htmlFor="lname">
              Last name
            </label>
            <input
              className="mr-8b form-inp"
              type="text"
              name="lname"
              id="lname"
              placeholder="Enter your last name"
              value={lname}
              onChange={e => setLname(e.target.value)}
            />
          </section>
          <section className="mr-12r flex-basis-20-l">
            <label className="mr-16t form-labl" htmlFor="dob">
              Date of birth
            </label>
            <input
              className="mr-8b form-inp"
              type="date"
              name="dob"
              id="dob"
              placeholder="Enter your date of birth"
              value={dob}
              onChange={e => setDob(e.target.value)}
            />
          </section>
          <section className="flex-basis-20-l  mr-12r">
            <label className="mr-16t form-labl" htmlFor="sem">
              Sem
            </label>
            <CustomSelect
              options={[
                "First1",
                "Second2",
                "Third3",
                "Fourth4",
                "Fifth5",
                "Sixth6",
                "Seventh7",
                "Eigth8"
              ]}
              isStatic={true}
              placeholder={"Enter your sem"}
              value={sem + "" || "First1"}
              selectValue={setSem}
              required={true}
            />
          </section>
          <section className="flex-basis-20-l">
            <label className="mr-16t form-labl" htmlFor="sex">
              Sex
            </label>
            <CustomSelect
              options={["Male", "Female", "Others"]}
              isStatic={true}
              placeholder={"Enter your sex"}
              value={sex + ""}
              selectValue={setSex}
              required={true}
            />
          </section>
          <section className="mr-12r flex-basis-20-l">
            <label className="mr-16t form-labl" htmlFor="email">
              Email
            </label>
            <input
              className="mr-8b form-inp"
              type="text"
              name="email"
              id="email"
              placeholder="Enter your email"
              value={email}
              onChange={e => setEmail(e.target.value)}
            />
          </section>
          <section className="mr-12r flex-basis-20-l">
            <label className="mr-16t form-labl" htmlFor="phone">
              Phone
            </label>
            <input
              className="mr-8b form-inp"
              type="tel"
              name="phone"
              id="phone"
              placeholder="Enter your phone"
              value={phone}
              onChange={e => {
                if (!isNaN(e.target.value))
                  setPhone(e.target.value.slice(0, 10));
              }}
            />
          </section>
          <section className="flex-basis-20-l">
            <label className="mr-16t form-labl" htmlFor="Brach">
              Branch
            </label>
            <CustomSelect
              options={[
                "Compter Science",
                "Electronics and Commnication",
                "Electrical and Electronics",
                "Civil",
                "Biotechnology",
                "Information Science"
              ]}
              isStatic={true}
              placeholder={"Enter your departmeent"}
              value={branch}
              selectValue={setbranch}
              required={true}
            />
          </section>
          <section className="flex-basis-70-l mr-16t mr-12r">
            <h3 className="fs-18 mr-4t mr-8b">Address</h3>
            <section className=" disp-flex fl-dir-col mr-12r">
              <section className="wi-100p">
                <label className="mr-4t form-labl" htmlFor="address_line1">
                  Address line 1
                </label>
                <input
                  className="mr-8b form-inp wi-100p"
                  type="text"
                  name="address_line1"
                  id="address_line1"
                  placeholder="Enter address line 1"
                  value={address_line1}
                  onChange={e => setaddress_line1(e.target.value)}
                />
              </section>
              <section className="wi-100p">
                <label className="mr-4t form-labl" htmlFor="address_line1">
                  Address line 2
                </label>
                <input
                  className="mr-8b form-inp wi-100p"
                  type="text"
                  name="address_line2"
                  id="address_line1"
                  placeholder="Enter address line 2"
                  value={address_line2}
                  onChange={e => setaddress_line2(e.target.value)}
                />
              </section>
              <section className="wi-100p">
                <label className="mr-4t form-labl" htmlFor="city">
                  City
                </label>
                <input
                  className="mr-8b form-inp wi-100p"
                  type="text"
                  name="city"
                  id="city"
                  placeholder="Enter your city"
                  value={city}
                  onChange={e => setCity(e.target.value)}
                />
              </section>
              <section className="wi-100p">
                <label className="mr-4t form-labl" htmlFor="dist_st">
                  District
                </label>
                <input
                  className="mr-8b form-inp wi-100p"
                  type="text"
                  name="dist"
                  id="dist"
                  placeholder="Enter your District"
                  value={dist}
                  onChange={e => setDist(e.target.value)}
                />
              </section>
              <section className="wi-100p">
                <label className="mr-4t form-labl" htmlFor="dist_st">
                  State
                </label>
                <input
                  className="mr-8b form-inp wi-100p"
                  type="text"
                  name="st"
                  id="st"
                  placeholder="Enter your State"
                  value={state}
                  onChange={e => setSt(e.target.value)}
                />
              </section>
              <section className="wi-100p">
                <label className="mr-4t form-labl" htmlFor="pin">
                  Pin
                </label>
                <input
                  className="mr-8b form-inp wi-100p"
                  type="text"
                  name="pin"
                  id="pin"
                  placeholder="Enter your pin"
                  value={pin}
                  onChange={e => {
                    if (!isNaN(e.target.value))
                      setPin(e.target.value.slice(0, 6));
                  }}
                />
              </section>
            </section>
          </section>
          <section className="mr-12r flex-basis-50-l disp-flex fl-dir-col">
            <label className="mr-16t form-labl" htmlFor="about_you">
              About you
            </label>
            <textarea
              className="mr-4b form-inp"
              name="about_you"
              id="about_you"
              placeholder="Enter a brief aboutmeription about yourself"
              value={aboutme}
              onChange={e => setaboutme(e.target.value)}
            ></textarea>
            <input
              className="mr-16t mr-4b form-inp wi-300 al-self-r"
              type="submit"
              value="Save changes"
            />
          </section>
        </form>
        {isDone ? (
          <div className="card mr-16 pd-16 bg-prim-grad wi-100p pos-fixed-top">
            Updates done successfully
          </div>
        ) : null}
      </section>
    </>
  );
}

export default Settings;
