import React, { useState } from "react";
import { useStoreActions, useStoreState } from "easy-peasy";
import AuthNav from "../AuthNav/AuthNav";
function Register({ currentAuth, setAuth }) {
  let [email, setEmail] = useState("");
  let [password, setPassword] = useState("");
  let [cpassword, setCPassword] = useState("");
  let errors = useStoreState(state => state.auth.errors);
  let registerSubmit = useStoreActions(action => action.auth.registerSubmit);
  let setErrors = useStoreActions(action => action.auth.setErrors);
  let pwdError = errors.find(err => err.type === "password");
  let cpwdError = errors.find(err => err.type === "cpassword");
  let emailError = errors.find(err => err.type === "email");
  return (
    <form
      id="auth-register"
      action=""
      className="flex-basis-50-l pd-64x pd-64t bg-light pd-64b"
      noValidate={true}
      onSubmit={e => {
        e.preventDefault();
        registerSubmit({ email, password, cpassword });
      }}
    >
      <h2 className="prim-text-clr">Register your account</h2>
      <p className="lh-16 mr-16t mr-32b">
        Please enter the same email as entered in the AtarV-Ed membership form.
        Verification email will be sent after form submission.
      </p>
      <AuthNav currentAuth={currentAuth} setAuth={setAuth} />
      <label className="mr-16t form-labl" htmlFor="email">
        Email
      </label>
      <input
        className="mr-8b form-inp wi-300"
        type="email"
        name="email"
        id="email"
        placeholder="Enter your email"
        value={email}
        onChange={e => {
          setEmail(e.target.value);
          if (errors.length) setErrors([]);
        }}
      />
      {emailError ? <p className="form-hint">{emailError.message}</p> : null}
      <label className="mr-16t form-labl" htmlFor="password">
        Password
      </label>
      <input
        className="mr-8b form-inp wi-300"
        type="password"
        name="password"
        id="password"
        placeholder="Enter your password"
        value={password}
        onChange={e => {
          setPassword(e.target.value);
          if (errors.length) setErrors([]);
        }}
      />
      {pwdError ? <p className="form-hint">{pwdError.message}</p> : null}
      <label className="mr-16t form-labl" htmlFor="cpassword">
        Confirm password
      </label>
      <input
        className="mr-8b form-inp wi-300"
        type="password"
        name="cpassword"
        id="cpassword"
        placeholder="Please confirm your password"
        value={cpassword}
        onChange={e => {
          setCPassword(e.target.value);
          if (errors.length) setErrors([]);
        }}
      />
      {cpwdError ? <p className="form-hint">{cpwdError.message}</p> : null}
      <input
        className="mr-16t mr-8b form-inp wi-300"
        type="submit"
        value="Submit"
      />
    </form>
  );
}

export default Register;
