import React, { useState, useEffect } from "react";
import ReportTable from "./ReportTable/ReportTable";
import { useStoreState, useStoreActions } from "easy-peasy";

function Report({ report }) {
  let [isTasksVisible, setTasksVisible] = useState(false);
  let isTaskLoading = useStoreState(state => state.admin.isTaskLoading);
  let setTaskLoading = useStoreActions(action => action.admin.setTaskLoading);
  let [tasks, setTasks] = useState(report.tasks);

  useEffect(() => {
    setTasks(report.tasks || []);
  }, [report.tasks]);

  return (
    <section className="pd-16 card bg-light report-inner mr-8t mr-16b  anim-trans">
      <h3 className="mr-8b">{report.activity_title}</h3>
      <p className="mr-8b tx-hier-2">{report.work}</p>
      <p className="fs-14 tx-hier-4">{report.activity_type}</p>

      {isTasksVisible ? (
        <section className="pd-16 task-table-holder anim-trans">
          <h3 className="mr-8b fs-14">Todays report</h3>
          <ReportTable
            tasks={tasks}
            setTask={setTasks}
            suid={report.suid}
            activityid={report.activityid}
            setTasksVisible={setTasksVisible}
            isTaskLoading={isTaskLoading}
          />
        </section>
      ) : null}
      <section id="imp-sec" className="disp-flex fl-jc-fe">
        <button
          className="btn mr-8t"
          onClick={e => {
            if (!isTasksVisible) {
              setTaskLoading(true);
              setTasksVisible(true);
            } else {
              setTasksVisible(false);
            }
          }}
        >
          View {isTasksVisible ? "Less" : "More"}
        </button>
      </section>
    </section>
  );
}

export default Report;
