import React from "react";

function Qualifications({ qualification }) {
  return qualification && qualification.length ? (
    qualification.map(quala => (
      <article
        className="pd-16y pd-8x disp-flex fl-jc-sb"
        key={quala.qualification}
      >
        <article>
          <h3 className="fs-18 mr-8b">{quala.qualification}</h3>
          <p className="fs-14 mr-8b tx-hier-2">{quala.college}</p>
          <p className="fs-16 tx-hier-3 mr-8b">{quala.board}</p>
        </article>
        <article className="al-self-c">
          <p className="mr-8b">Grade</p>
          <p className="fs-32 bold-josef mr-8b tx-hier-4">{quala.grade}</p>
        </article>
      </article>
    ))
  ) : (
    <p className="fs-16 tx-hier-3 mr-8b">No qualification added</p>
  );
}

export default Qualifications;
