import React from "react";

function AuthNav({ currentAuth, setAuth }) {
  function resolveAuthClassNclassName(authType) {
    return authType === currentAuth
      ? "pd-24x pd-12y current"
      : " pd-24x pd-12y";
  }
  return (
    <nav id="auth-nav" className="nav-t1">
      <ul className="mr-32b">
        <li>
          <a
            href="#auth-register"
            className={resolveAuthClassNclassName("Register")}
            onClick={e => {
              e.preventDefault();
              setAuth("Register");
            }}
          >
            Register
          </a>
        </li>
        <li>
          <a
            href="#auth-login"
            className={resolveAuthClassNclassName("Login")}
            onClick={e => {
              e.preventDefault();
              setAuth("Login");
            }}
          >
            Login
          </a>
        </li>
      </ul>
    </nav>
  );
}

export default AuthNav;
