import React, { useState, useEffect } from "react";
import { useStoreState, useStoreActions } from "easy-peasy";
import Register from "./Register/Register";
import { Redirect } from "react-router-dom";
import Login from "./Login/Login";
import WelcomeSection from "./WelcomeSection/WelcomeSection";
import "./auth.css";

function Auth() {
  let [currentAuth, setAuth] = useState("Login");
  let getProfile = useStoreActions(action => action.profile.getProfile);
  let getAllUsers = useStoreActions(action => action.auth.getAllUsers);
  let isAuth = useStoreState(state => state.auth.isAuth);
  useEffect(() => {
    if (!isAuth) {
      if (localStorage.getItem("token")) {
        getAllUsers(getProfile);
      }
    }
  }, [isAuth]);
  if (!isAuth)
    return (
      <section id="auth" className="disp-flex">
        <WelcomeSection />
        {currentAuth === "Register" ? (
          <Register currentAuth={currentAuth} setAuth={setAuth} />
        ) : (
          <Login currentAuth={currentAuth} setAuth={setAuth} />
        )}
      </section>
    );
  else
    return (
      <Redirect to={currentAuth === "Register" ? "/settings" : "/activities"} />
    );
}

export default Auth;
