import React, { useEffect, useState } from "react";
import Reports from "../Reports/Reports";
import MainNav from "../MainNav/MainNav";
import LoadingScreen from "../../UI/LoadingScreen";
import ReportQuery from "../Reports/Report/ReportQuery/ReportQuery";
import { useStoreState, useStoreActions } from "easy-peasy";
import AdminRecruitTeam from "../AdminRecruitTeam/AdminRecrutTeam";

function Activities() {
  let { type: authType } = useStoreState(state => state.auth.profile);
  let activities = useStoreState(state => state.admin.activities);
  let isLoading = useStoreState(state => state.admin.isLoading);
  let fetchActivities = useStoreActions(action => action.admin.fetchActivities);
  let getProfile = useStoreActions(action => action.profile.getProfile);
  let [isVisible, setVisible] = useState(false);
  useEffect(() => {
    if (isLoading) {
      fetchActivities(authType);
      getProfile();
    }
  }, [authType, isLoading]);
  return (
    <>
      <MainNav />
      {isVisible ? (
        <AdminRecruitTeam setVisible={setVisible} isVisible={isVisible} />
      ) : null}
      <section id="report-details" className="flex-basis-70-l">
        <header id="reports-header" className="pd-16 mr-16x flex-basis-50-l">
          <h2 className="fs-24">Acitve Activities</h2>
        </header>
        <section className=" pd-32 mr-16">
          {authType === "A" ? <ReportQuery setVisible={setVisible} /> : null}
          {activities && activities.length ? (
            <Reports activities={activities} />
          ) : (
            <section className="pd-16 card bg-light report-inner mr-8t">
              <h3 className="mr-8b">No active activities</h3>
            </section>
          )}
          {isLoading ? <LoadingScreen /> : null}
        </section>
      </section>
    </>
  );
}

export default Activities;
