import React, { useState } from "react";
import { useStoreActions, useStoreState } from "easy-peasy";
import CustomSelect from "../../UI/CustomSelect";

function AdminRecruitTeam({ setVisible }) {
  let users = useStoreState(state => state.auth.users);

  let [field, setField] = useState("web Development");
  let [members, setMembers] = useState([]);
  let [superVisor, setSupervisor] = useState("");
  let [activityType, setActivityType] = useState("");
  let [activityTitle, setActivityTitle] = useState("");
  let submitActivityTeam = useStoreActions(
    action => action.admin.submitActivityTeam
  );
  function setSelectMembers(payload) {
    setMembers([...members, payload]);
  }
  return (
    <div className="pos-fixed-top bg-dark-trans wi-100p hi-100p z-3">
      <form
        action="POST"
        onSubmit={e => {
          e.preventDefault();
          submitActivityTeam({
            work: field,
            user_id: members.map(str => parseInt(str.match(/\d+/)[0])),
            super_id: parseInt(superVisor.match(/\d+/)[0]),
            activity_type: activityType,
            title: activityTitle
          });
          setVisible(false);
        }}
      >
        <section className="pd-16 mr-16">
          <section className="pd-16 card card-color-yellow tx-trans-lv1 report-inner mr-8t">
            <h3 className="mr-16b fs-24">Start a new activity</h3>
            <div>
              <label className="mr-16t form-labl" for="field">
                Field of work
              </label>
              <CustomSelect
                options={[
                  "IOT",
                  "Artificial Intelligence",
                  "android development",
                  "web development"
                ]}
                placeholder={"Enter the field of work"}
                selectValue={setField}
                value={field}
                isStatic={true}
                reqired={true}
              />
            </div>
            <section className="mr-12r">
              <label className="mr-16t form-labl" for="title">
                Activity title
              </label>
              <input
                className="mr-8b form-inp wi-100p"
                type="text"
                name="title"
                id="title"
                placeholder="Enter the activity's title"
                value={activityTitle}
                onChange={e => {
                  setActivityTitle(e.target.value);
                }}
                required={true}
              />
            </section>
            <section className="mr-12r">
              <label className="mr-16t form-labl" for="work">
                Activity type
              </label>
              <CustomSelect
                options={["Project", "Workshop", "Training"]}
                placeholder={"Enter activity type "}
                selectValue={setActivityType}
                value={activityType}
                isStatic={false}
                reqired={true}
              />
            </section>
            <section className="mr-12r">
              <label className="mr-16t form-labl" for="members">
                Add team members
              </label>
              <CustomSelect
                options={users
                  .filter(
                    user =>
                      !members.some(member => member.includes(user.user_id))
                  )
                  .map(
                    ({ fname, lname, user_id }) =>
                      fname + " " + lname + `${user_id}`
                  )}
                placeholder={"Add members "}
                selectValue={setSelectMembers}
                value={""}
                isStatic={false}
                required={false}
              />
            </section>
            {members.map(member => (
              <div
                key={member}
                onClick={e => {
                  setMembers(
                    members.filter(existMember => {
                      if (member === superVisor) setSupervisor("");
                      return existMember !== member;
                    })
                  );
                }}
                className="card pd-8x cu-po pd-4y disp-ib bg-light mr-16t  mr-4r"
              >
                {member.replace(/\d+/, "") + " X"}
              </div>
            ))}
            <section className="mr-12r">
              <label className="mr-16t form-labl" for="supervisor">
                Appoint supervisor
              </label>
              <CustomSelect
                options={members}
                placeholder={"Select supervisor "}
                selectValue={setSupervisor}
                value={superVisor}
                isStatic={false}
                required={false}
              />
            </section>
            <section id="imp-sec" className="disp-flex fl-jc-fe">
              <button
                className="btn mr-8t mr-12r"
                onClick={e => setVisible(false)}
              >
                Cancel
              </button>
              <button className="btn mr-8t">Assign activity</button>
            </section>
          </section>
        </section>
      </form>
    </div>
  );
}

export default AdminRecruitTeam;
